import React, { Component } from 'react';
import { Tooltip, OverlayTrigger } from 'react-bootstrap';
import { getRoiData, updateRoiData } from '../../../../Services/Actions/homeActions';
import { debounce } from '../../../../Services/Utils/utils';
import { updateUserInfo } from '../../../../Services/Actions/loginActions';
import { setLocalStorage, getLocalStorage } from '../../../../Services/Utils/storage';
import { connect } from 'react-redux';
import { roiItems2, roiItems } from '../../../../Services/Utils/config';
class UgcROI extends Component {
    state = {
        roiValue: 10,
        mediaValue: 0,
        roiData: { ROIValue: 0, approved: 0, collected: 0 },
        roiDataRequest: true,
    }

    componentDidMount() {
        if (!getLocalStorage("verify_email")) {
            this.onLoadROIData()
        }
    }

    onLoadROIData = () => {
        const { roiData } = this.props.roiData
        var self = this;
        this.setState({ roiDataRequest: true })
        if (roiData && Object.keys(roiData).length > 0) {
            self.setState({ roiDataRequest: false, roiData: roiData })
        }
        else {
            getRoiData().then((response) => {
                const { responseData } = response.data;
                if (responseData && Object.keys(responseData).length > 0) {
                    self.setState({ roiDataRequest: false, roiData: responseData })
                    this.props.placeRoiData(responseData)

                }
                else {
                    self.setState({ roiDataRequest: false })
                }
            }).catch((ex) => {
                self.setState({ roiDataRequest: false }, () => { })
            })
        }
    }

    roiCalculate = (event) => {
        const regex = /^[0-9\b]+$/;
        if (event.target.value === '' || regex.test(event.target.value) && event.target.value.length < 4) {
            let { roiData } = this.state
            roiData.ROIValue = event.target.value
            this.setState({
                roiData,
                roiValue: event.target.value,
                mediaValue: 23 * 132
            });
        }
    }

    setRoiData = debounce((event) => {
        var self = this
        const regex = /^[0-9\b]+$/;
        if (event.target.value === '' || regex.test(event.target.value) && event.target.value.length < 4) {
            updateRoiData({ ROIValue: event.target.value }).then((response) => {
                const { responseData } = response.data;
                let userData = JSON.parse(getLocalStorage('user_data'))
                if (userData && Object.keys(userData).length > 0) {
                    userData.UserRule.ROIValue = parseInt(responseData.ROIValue)
                    setLocalStorage('user_data', JSON.stringify(userData))
                }
                this.props.updateUserRoi()

            }).catch(ex => { })
        }
    });

    onUpdateROI = event => {
        this.setRoiData(event)
    }

    onROIinputFocus = (event) => event.target.select();

    render() {
        const { roiData } = this.state;
        const { userData } = this.props
        return <div className="card card-custom card-stretch gutter-b text-decoration-none">
            <div className="card-header pt-3 border-0">
                <h5 className="text-gray-dark fs-6 mb-0">UGC ROI Calculator</h5>
            </div>
            <div className="card-body d-flex flex-column pb-0">
                <span className="fs-7 fw-semibold text-ellipsis mb-2 text-gray-dark">Collected Assets</span>
                <div className="row gx-3">
                    {roiItems.map(item=>{
                        return <div className="col-sm">
                            <div className="card card-custom card-stretch gutter-b">
                                <div className={`card-body d-flex flex-column ${item.value == 'potential' ? "pe-1" : 'roi_cal'}`}>
                                    <div className="flex-grow-1">
                                        <span className="fw-semibold text-gray-500 mb-2 d-flex">{item.name}
                                            <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">{item.tip}</Tooltip>}>
                                                <i className="ri-information-fill icon-6 ms-1 fw-normal"></i>
                                            </OverlayTrigger>
                                        </span>
                                        <div className="d-flex align-items-center justify-content-between">
                                            <div className="d-flex align-items-center">
                                                <div className={`text-gray-dark fw-bold h5 ${item.value=='estimate' ? 'me-1' :'pt-xl-1'} mb-0`}>{item.value=='collect' ? roiData.collected : item.value=='potential' ? `$${roiData.ROIValue * roiData.collected}`:"$"}</div>
                                                {item.value == 'estimate' ? <input type="text" className="form-control form-control-sm fs-5 py-0 text-gray-dark fw-bold" style={{ maxWidth: 100 }} onChange={this.roiCalculate} onKeyUp={this.onUpdateROI} onFocus={this.onROIinputFocus} value={roiData.ROIValue} disabled={userData && userData.hasOwnProperty('collaborator') ?(userData.collaborator == '' || userData.collaborator.activeAccount == '0' || userData.collaborator.permissions == '') ? false : true: false} /> : null}
                                            </div>
                                            {item.value != "potential" ? <div className="text-gray-dark fw-bold h5 pt-xl-1 mb-0">
                                                <span className="svg-icon text-gray-500 me-0">
                                                    <i className={item.icon}></i>
                                                </span>
                                            </div> : null}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    })}
                </div>

                <span className="fs-7 fw-semibold text-ellipsis mb-2 text-gray-dark">Approved Assets</span>
                <div className="row gx-3">
                    {roiItems2.map(item=>{
                        return <div className="col-sm">
                            <div className="card card-custom card-stretch gutter-b">
                                <div className="card-body d-flex flex-column">
                                    <div className="flex-grow-1">
                                        <span className="fw-semibold text-gray-500 mb-2 d-flex">{item.name}
                                            <OverlayTrigger placement="top" overlay={<Tooltip id="button-tooltip-2">{item.tip}</Tooltip>}>
                                                <i className="ri-information-fill icon-6 ms-1 fw-normal"></i>
                                            </OverlayTrigger>
                                        </span>
                                        <div className="text-gray-dark fw-bold h5 pt-xl-1 mb-0">{item.value == 'collect' ? `${roiData.collected}`: item.value == 'approve' ? `${roiData.approved}` : `$${roiData.approved * roiData.ROIValue}`}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    })}
                </div>
            </div>
        </div>
    }
}

const mapStateToProps = state => {
    return {
        roiData: state.ugcRoi
    }
}

const mapDispatchToProps = dispatch => ({
    placeRoiData: (data) => dispatch({ type: "SET_ROI_DATA", payload: data }),
    updateUserRoi: () => updateUserInfo(dispatch)
})

export default connect(mapStateToProps, mapDispatchToProps)(UgcROI)